import { Injectable } from "@angular/core";

@Injectable({
  providedIn: "root"
})
export class ModalService {
  opened = false;

  title = "";

  content: "";

  /**
   * Funcin que abre una notificacin modal
   * @param {String} title
   * @param {String} content
   */
  open(title, content) {
    this.opened = true;
    this.title = title;
    this.content = content;
  }

  /**
   * Funcin que cierra una notificacin modal
   */
  close() {
    this.opened = false;
  }
}
