import { Injectable } from "@angular/core";
import { Error } from "../core.models";

@Injectable({
  providedIn: "root"
})
export class ToastService {
  toasts: Toast[] = [];

  /**
   * Funcin para aadir una notificacin toast
   * @param {String} title
   * @param {String} message
   * @param {String} variant
   * @param {Number} delay
   */
  addToast(
    title: string,
    message: string,
    variant: string = "primary",
    delay: number = 6000
  ) {
    this.toasts.unshift({
      title,
      message,
      variant
    });

    if (delay) {
      this.delayAndRemove(delay);
    }
  }

  /**
   * Funcin para aadir una notificacin toast de error
   * @param {Object} error
   * @param {Number} delay
   */
  addErrorToast(error, delay?: number) {
    this.toasts.unshift({
      title: "Error",
      message: error.message,
      variant: "danger"
    });

    if (delay) {
      this.delayAndRemove(delay);
    }
  }

  /**
   * Funcin que comprueba si un toast es de error o no
   * @param {Object} toast
   * @return {Boolean}
   */
  isErrorToast(toast: Toast) {
    if (toast.variant === "error") {
      return true;
    }
    return false;
  }

  /**
   * Funcin que muestra la notificacin toast por un tiempo estipulado
   * @param {Number} milliseconds
   */
  delayAndRemove(milliseconds: number) {
    setTimeout(() => {
      this.toasts.pop();
    }, milliseconds);
  }

  /**
   * Funcin que elimina la notificacin toast del array donde estn guardados
   * @param {Number} index
   */
  remove(index?: number) {
    if (index) {
      this.toasts.splice(index, 1);
    } else {
      this.toasts.pop();
    }
  }
}

interface Toast {
  title: string;
  message: string;
  variant?: string;
  delay?: number;
}
